<!--suppress HtmlUnknownTag, CheckEmptyScriptTag, HtmlUnknownAttribute -->
<template>
  <v-container fluid>
    <v-row style="padding: 0 50px">
      <v-col cols="12" lg="5">
        <h2>{{ title }}</h2>
      </v-col>

      <v-col cols="12" lg="2">
        <v-icon style="float:left; transform: translateY(15px)"
                @click="interboxX = 50">mdi-arrow-collapse-horizontal</v-icon>
        <v-icon style="float:right; transform: translateY(15px)"
                @click="interboxX = 500">mdi-arrow-expand-horizontal</v-icon>
        <v-slider style="transform: translateY(10px)"
                  v-model="interboxX"
                  min="50" step="1" max="500" />
      </v-col>

      <v-col cols="12" sm="5" lg="2">
        <v-select solo
                  class="dashed-border-within"
                  label="View another"
                  :items="dropdownItems"
                  v-model="dropdownSelection" />
      </v-col>

      <v-col cols="12" sm="7" lg="3">
        <v-expansion-panels class="dashed-border">
          <v-expansion-panel hover focusable disabled>
            <v-expansion-panel-header>
              Make your own! (coming soon)
            </v-expansion-panel-header>
            <v-expansion-panel-content>
              <p>
                Steps 1–5: Follow instructions on... . Beware! They are very technical!
              </p><p>
                Step 6: [upload]
              </p><p>
                Step 7: Download [coming later]
              </p>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </v-col>
    </v-row>

    <div style="overflow-x: scroll">
      <svg :width="playlists.length * (boxWidth + interboxX) - interboxX + 2 * prePostBoxX"
           :height="maxTracksInPlaylist * (boxHeight + interboxY) + interboxY + offsetY">
        <g v-for="(playlist,x) in playlists">

          <text :x="textOffsetX + prePostBoxX + x * (interboxX + boxWidth)"
                :y="textOffsetY"
                fill="white">
            {{ playlist.name }}
          </text>

          <path v-for="connector in skippingConnectors"
                :d="connectorPath(connector)"
                stroke="grey"
                stroke-width="5"
                opacity=".5"
                fill="none" />

          <path v-for="connector in immediateConnectors"
                :d="connectorPath(connector)"
                stroke="red"
                stroke-width="2"
                fill="none" />

          <rect v-for="(ignore,y) in playlist.tracks"
                :x="prePostBoxX + x * (interboxX + boxWidth)"
                :y="offsetY + interboxY + y * (interboxY + boxHeight)"
                :width="boxWidth"
                :height="boxHeight"
                rx="7" ry="7"
                style="fill:black; stroke:grey; stroke-width:1; opacity:.77" />

          <g v-for="(track,y) in playlist.tracks">
            <text :x="textOffsetX + prePostBoxX + x * (interboxX + boxWidth)"
                  :y="textOffsetY + offsetY + interboxY + y * (interboxY + boxHeight)"
                  fill="white"
                  font-weight="bold">
              {{ track.name }}
            </text>

            <text v-for="(line,i) in track.additionalLines"
                  :x="textOffsetX + prePostBoxX + x * (interboxX + boxWidth)"
                  :y="(i+1) * intertextY + textOffsetY + offsetY + interboxY + y * (interboxY + boxHeight)"
                  fill="white">
              {{ line }}
            </text>
          </g>
        </g>
      </svg>
    </div>

  </v-container>
</template>

<script>
import AxiosHelper from '../components/AxiosHelper.vue';
import router from '../plugins/router'

export default {
  mixins: [AxiosHelper],
  props: ['indexKey', 'playlist'],

  data: () => ({

    ///  Magic constants  ///

    boxWidth: 280,
    boxHeightBase: 36,
    prePostBoxX: 50,
    interboxX: 100,
    interboxY: 30,
    offsetY: 25,

    textOffsetX: 10,
    textOffsetY: 24,
    intertextY: 22,

    ///  Loaded by calls  ///

    index: {},
    title: 'Loading...',
    playlists: [],
    immediateConnectors: [],
    skippingConnectors: [],

    ///  UI elements  ///

    dropdownItems: [],
    dropdownSelection: null

  }),

  computed: {
    boxHeight() {
      const maxAdditionalLines = (this.playlists || [])
        .flatMap(pl => pl.tracks || [])
        .map(track => (track.additionalLines || []).length)
        .reduce((a,b) => Math.max(a,b), 0)
      return this.boxHeightBase + maxAdditionalLines * this.intertextY;
    },

    maxTracksInPlaylist() {
      return (this.playlists || [])
        .map(pl => (pl.tracks || []).length)
        .reduce((a,b) => Math.max(a,b), 0)
    }
  },

  methods: {
    connectorPath(connector) {
      const startX = this.prePostBoxX + this.boxWidth + (this.interboxX + this.boxWidth) * (connector.startCol)
      const endX = this.prePostBoxX + (this.interboxX + this.boxWidth) * (connector.endCol)
      const midX = (startX + endX) / 2
      const startY = this.offsetY + this.interboxY + this.boxHeight / 2 + (this.interboxY + this.boxHeight) * (connector.startRow)
      const endY = this.offsetY + this.interboxY + this.boxHeight / 2 + (this.interboxY + this.boxHeight) * (connector.endRow)

      return `M ${startX} ${startY} c ${midX-startX} ${startY-startY} ${midX-startX} ${endY-startY} ${endX-startX} ${endY-startY}`
    },

    loadIndex() {
      this.plainAxios('loading playlist index', axios => axios.get(this.$dataUrl + '/playlists/' + this.indexKey + '.json'),
          data => {
            this.index = data;
            this.dropdownItems = Object.keys(data);
          })
    },

    loadPlaylistData() {
      const playlistKey = this.index[this.playlist];
      if (!playlistKey) {
        console.error('Index busted! Could not find ' + this.playlist);
        return;
      }

      this.title = 'Loading...';
      this.playlists = [];
      this.immediateConnectors = [];
      this.skippingConnectors = [];

      this.plainAxios('loading playlist data', axios => axios.get(this.$dataUrl + '/playlists/' + playlistKey + '.json'),
        data => {
          this.title = data.title;
          this.playlists = data.playlists;
          this.immediateConnectors = data.immediateConnectors;
          this.skippingConnectors = data.skippingConnectors;
          this.dropdownSelection = null;
        })
    }
  },

  watch: {

    dropdownSelection(newValue) {
      if (newValue)
        router.push({params: {playlist: newValue}})
    },

    indexKey() {
      this.loadIndex();
    },

    index() {
      this.loadPlaylistData();
    },

    playlist() {
      this.loadPlaylistData();
    }
  },

  mounted() {
    this.loadIndex();
  }
}
</script>
<style>
.dashed-border, .dashed-border-within .v-input__slot {
  border: 1px dashed #b5e853
}
</style>